.blog-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px;
  }
  
  .blogpost-preview {
    color: white;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin-bottom: 20px;
    padding: 20px;
    width: 100%;
    max-width: 800px;
  }
  
  .blogpost-preview h2 {
    margin: 0;
  }
  
  .blogpost-preview p {
    margin-top: 10px;
  }
  
  .blogpost-preview small {
    color:white;
  }
  