.blogpost {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px;
  }
  
  .blogpost-content {
    color: white;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin-bottom: 20px;
    padding: 20px;
    width: 100%;
    max-width: 800px;
  }
  
  .blogpost-content h1 {
    margin: 0;
  }
  
  .blogpost-content p {
    margin-top: 10px;
  }
  
  .blogpost-content small {
    color: white;
  }
  