.overview-container {
  background-color: #F5F5F5;
  color: black;
  padding: 20px;
  margin: 20px auto;
  border-radius: 15px;
  border: 2px solid #474E5C;
  width: 90%;
  max-width: 90%;
}

.centered {
  text-align: center;
}

.table-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin-bottom: 20px;
}

.table {
  border-collapse: collapse;
  width: 100%;
  text-align: center;
}

.table th {
  background-color: #474E5C;
  color: white;
  padding: 10px;
}

.table td {
  padding: 10px;
  border-top: 1px solid #ddd;
}

.table tr:nth-child(even) {
  background-color: #f2f2f2;
}
