
.rankings-container {
  border: 2px solid white;
  border-radius: 15px;
  padding: 20px;
  padding-left: 50px; /* Adjust this to control padding on the left side */
  margin: 0 auto;
  width: 90%;  /* Increase from 70% to 90% */
  max-width: 90%; /* Increase from 800px to 90% */
  background-color: rgb(46, 45, 45);
  color: white;
  font-family: 'Roboto', sans-serif;
}

@media only screen and (max-width: 600px) {
  .rankings-container {
    width: 95%;  /* Increase from 90% to 95% */
    padding: 5px; /* Reduce padding to 5px for more width on small screens */
    padding-left: 10px; /* You may want to adjust this to keep your content visually centered */
  }
}



