.card {
  width: 90%;
  max-width: 1000px;
  height: 700px;
  position: relative;
  perspective: 1000px;
  margin: 100px auto;
}

.card .card-front,
.card .card-back {
  background-color: #2d2d2d;
  color: black;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-radius: 10px;
  background: #F5F5F5;
  transition: transform 0.6s;
  backface-visibility: hidden;
  overflow: auto;
  word-wrap: break-word;
}

.card .card-front {
  z-index: 2;
  transform: rotateY(0deg);
}

.card .card-back {
  transform: rotateY(180deg);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;  /* Align items to the left */
  text-align: left;  /* Align text to the left */
}

.card.flipped .card-front {
  transform: rotateY(-180deg);
}

.card.flipped .card-back {
  transform: rotateY(0deg);
}

.view-more-btn,
.view-less-btn {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background: #007bff;
  color: #F5F5F5;
  cursor: pointer;
  align-self: center; /* Aligns the button to the center */
}
.ranking-image {
  width: 300px;
  height: 300px;
  object-fit: cover;
  border-radius: 50%; /* Will create a circle if the image is a square */
  margin: auto;
}


.card-content {
  text-align: center;
}

.ranking-title-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; 
  font-size: 1.25em;
  margin: 0 0 15px 0;
}

.ranking-number {
  font-size: 2em;
  font-weight: bold;
  margin-bottom: 5px;
}

.ranking-name {
  text-align: center;
  font-size: 2em;
}

.card-front {
  display: flex;
  flex-direction: column; 
  justify-content: space-between;
}

.view-more-btn {
  margin-top: 20px;
}

.ranking-score,
.ranking-total-investment {
  font-size: 1.25em; /* Adjust to desired size */
  text-align: center; /* Centers the text */
}

.ranking-score p,
.ranking-total-investment p {
  margin: 0; /* Removes default paragraph margins that could offset alignment */
}

@media only screen and (max-width: 600px) {
  .card {
    width: 95%;
  }
}
